import { Component, OnInit } from '@angular/core';
import { cribs } from '../../data/cribs';
import { MapService } from '../../services/map.service';
import { GlobalDataService } from '../../services/global-data.service';

@Component({
  selector: 'app-map-view',
  templateUrl: './map-view.component.html',
  styleUrls: ['./map-view.component.css']
})
export class MapViewComponent implements OnInit {
  private mapView: any = {
    esriMapView1: {}
  };

  cribs: Array<any> = cribs;
  constructor(private mapService: MapService, private _globalData: GlobalDataService) { }

  ngOnInit() {
    this.mapService.loadEsri().then(() => {
      this.initMap();
    }, (err) => {
      console.log('Wrong loading maps: ' + err);
    });
  }

  initMap() {
    this.mapView.esriMapView1 = new this.mapService.esriMapView({
      container: "viewDiv",
      map: this.mapService.map1,
      zoom: 4,
      center: [-94.91, 38.67]
    });


    var searchWidget = new this.mapService.searchWidget({
      view: this.mapView.esriMapView1
    });

    var baseMapToogle = new this.mapService.basemapToggleWidget({
      view: this.mapView.esriMapView1,
      nextBasemap: "hybrid"
    });

    this.mapView.esriMapView1.ui.remove("zoom");
    var zoomWidget = new this.mapService.zoomWidget({
      view: this.mapView.esriMapView1,
      layout: "horizontal"
    });

    this.mapView.esriMapView1.ui.add([
      {
        component: zoomWidget,
        position: "bottom-right",
        index: 1
      },
      {
        component: searchWidget,
        position: "bottom-right",
        index: 2
      }, {
        component: baseMapToogle,
        position: "bottom-left",
        index: 3
      }
    ]);

    this.mapView.esriMapView1.on('click', (e) => {
      this.getGeometrySegmentInformation(e);
      //alert('I am here');
    });

    this.mapView.esriMapView1.map.add(this.mapService.highlightSelectedGraphicsLayer);
  }

  getGeometrySegmentInformation(data: any) {
    this.mapService.highlightSelectedGraphicsLayer.removeAll();
    let identityTaskGeometry = new this.mapService.identifyTask(this._globalData.getData('pipeLineUrl'));
    let paramsGeometry = new this.mapService.identifyParameters();
    paramsGeometry.tolerance = 3;
    paramsGeometry.layerIds = [0];
    paramsGeometry.layersOption = "visible";
    paramsGeometry.geometry = data.mapPoint;
    paramsGeometry.returnGeometry = true;
    paramsGeometry.mapExtent = this.mapView.esriMapView1.extent;
    identityTaskGeometry.execute(paramsGeometry).then((response) => {

      if (response.results.length == 0) {
        this.mapService.bInfoTemplatePanel = false;
      }
      else {
       this.mapService.attrAREANAME = response.results[0].feature.attributes.AREANAME;
       this.mapService.attrPOP2000 = response.results[0].feature.attributes.POP2000;
       this.mapService.attrST = response.results[0].feature.attributes.ST;
       this.mapService.attrCLASS= response.results[0].feature.attributes.CLASS;
        this.mapService.bInfoTemplatePanel = true;
        this.highlightGeometry(response.results[0].feature.geometry);
      }
    }, (error) => {
      this.mapService.bInfoTemplatePanel = false;
      console.log(error);
    });
  }

  highlightGeometry(data: any) {
    var polylineGraphic = new this.mapService.graphic({
      geometry: data,
      symbol: {
        type: "simple-marker",
        size: 18,
        color: "#00FFFF",
        outline: {
          color: "rgba(255, 255, 255, 0.5)",
          width: 0.75
        }
      }
    });

    this.mapService.highlightSelectedGraphicsLayer.add(polylineGraphic);
  }
}


