    renderCities(){

        var otherSymbol = {
            type: "simple-fill",  // autocasts as new SimpleFillSymbol()
            color: [ 51,51, 204, 0.9 ],
            style: "solid",
            outline: {  // autocasts as new SimpleLineSymbol()
              color: "white",
              width: 1
            }
          };

          var redSymbol = {
            type: "simple-fill",  // autocasts as new SimpleFillSymbol()
            color: [ 255, 0, 0, 0.9 ],
            style: "solid",
            outline: {  // autocasts as new SimpleLineSymbol()
              color: "white",
              width: 1
            }
          };

          var greenSymbol = {
            type: "simple-fill",  // autocasts as new SimpleFillSymbol()
            color: [ 0,0, 255, 0.9 ],
            style: "solid",
            outline: {  // autocasts as new SimpleLineSymbol()
              color: "white",
              width: 1
            }
          };          
        var citiesRenderer = {
              type: "unique-value",
          field: "ST",
          uniqueValueInfos: [{
            value: "NY",
            symbol: {
              type: "simple-marker",
              size: 9,
              color: "rgb(0, 92, 230)",
              outline: {
                color: "rgba(255, 255, 255, 0.5)",
                width: 0.75
              }
            }
          }, {
            value: "CA",
            symbol: {
              type: "simple-marker",
              size: 9,
              color: "rgb(255, 20, 20)",
              outline: {
                color: "rgba(255, 255, 255, 0.5)",
                width: 0.75
              }
            }
          }]
        };

          /*type: "unique-value",
          field: "ST",
          uniqueValueInfos: [{
            value: "Obama",
            symbol: {
              type: "simple-marker",
              size: 9,
              color: "rgb(0, 92, 230)",
              outline: {
                color: "rgba(255, 255, 255, 0.5)",
                width: 0.75
              }
            }
          }, {
            value: "McCain",
            symbol: {
              type: "simple-marker",
              size: 9,
              color: "rgb(255, 20, 20)",
              outline: {
                color: "rgba(255, 255, 255, 0.5)",
                width: 0.75
              }
            }
          }*/

          return citiesRenderer;
    }


    getSynthesizedRisk() {
        var sysRiskRenderer = {
            type: "class-breaks", // autocasts as new ClassBreaksRenderer()
            field: "OXYGEN",
            defaultSymbol: {
                type: "simple-fill", // autocasts as new SimpleFillSymbol()
                color: "gray",
                outline: {
                    width: 0.5,
                    color: "white"
                }
            },
            defaultLabel: "no data",
            classBreakInfos: [{
                minValue: -99,
                maxValue: 1000000,
                symbol: this.severeSym,
                label: "SEVERE"
            }]
        };

        return sysRiskRenderer;
    }

    getRendererCO2() {

        //-9999 to 8.9999999
        //9 to 14.999999
        //15 to 24.999999
        //25 to 39.999999
        //40 to 99999.999999

        var rendererCO2 = {
            type: "class-breaks", // autocasts as new ClassBreaksRenderer()
            field: "CO2",
            defaultSymbol: {
                type: "simple-fill", // autocasts as new SimpleFillSymbol()
                color: "gray",
                outline: {
                    width: 0.5,
                    color: "white"
                }
            },
            defaultLabel: "no data",
            classBreakInfos: [{
                minValue: 0,
                maxValue: 8.9999,
                symbol: this.severeSym
            }, {
                minValue: 9,
                maxValue: 14.9999,
                symbol: this.highSym
            }, {
                minValue: 15,
                maxValue: 24.9999,
                symbol: this.moderateSym
            }, {
                minValue: 25,
                maxValue: 39.9999,
                symbol: this.lowModSym
            }, {
                minValue: 40,
                maxValue: 10000,
                symbol: this.lowSym
            }]
        };
        return rendererCO2;
    }


    getRendererWeight(weight)
    {
       //H2S
       //-9999 to 0.0149999
       //0.0150000 to 0.0199999
       //0.200000 to 0.0490000
       //0.0500000 to 0.099999
       //0.1000 to 9999.99999

        var rendererH2SPPM = {
            type: "class-breaks", // autocasts as new ClassBreaksRenderer()
            field: weight,
            defaultSymbol: {
                type: "simple-fill", // autocasts as new SimpleFillSymbol()
                color: "gray",
                outline: {
                    width: 5,
                    color: "white"
                }
            },
            defaultLabel: "no data",
            classBreakInfos: [{
                minValue: 0,
                maxValue: 2,
                symbol: this.greenClr
            }, {
                minValue: 2,
                maxValue: 4,
                symbol: this.orangeClr
            }, {
                minValue: 4,
                maxValue: 5,
                symbol: this.redClr
            }]
        };
        return rendererH2SPPM;
    }

