import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import {  MapService } from './services/map.service';
import {  RendererService } from './services/renderer.service';
import { ApiService } from './services/api.service';
import { GlobalDataService } from './services/global-data.service';
import { loadModules } from 'esri-loader';
import { ScrollbarModule } from 'ngx-scrollbar';
import { HttpModule, JsonpModule } from '@angular/http';
import { AngularDraggableModule } from 'angular2-draggable';

import { AppComponent } from './app.component';
import { MapViewComponent } from './components/map/map-view.component';
import { IconHeaderComponent } from './components/map/icon-header/icon-header.component';
import { InfoTemplateComponent } from './components/map/info-template/info-template.component';
import { PipelineDetailsComponent } from './components/map/pipeline-details/pipeline-details.component';
import { LegendComponent } from './components/map/legend/legend.component';
import { TableOfContentsComponent } from './components/map/table-of-contents/table-of-contents.component';

import { MatTableModule } from '@angular/material/table';
import { MatPaginatorModule } from '@angular/material';
import { HttpClientModule } from '@angular/common/http';

import { PipelineService } from './services/pipeline.service';

import { CdkTableModule } from '@angular/cdk/table';
import { DataSource  } from '@angular/cdk/table';
import { UsertableComponent } from './components/map/usertable/usertable.component';

import { UserService } from './services/user.service';
import { PipelineDatatableComponent } from './components/map/pipeline-datatable/pipeline-datatable.component';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';

@NgModule({
  declarations: [
    AppComponent,
    MapViewComponent,
    IconHeaderComponent,
    InfoTemplateComponent,
    PipelineDetailsComponent,
    LegendComponent,
    TableOfContentsComponent,
    UsertableComponent,
    PipelineDatatableComponent
  ],
  imports: [
    BrowserModule,
    HttpModule,
    JsonpModule,
    MatTableModule,
    MatPaginatorModule,
    HttpClientModule,
    CdkTableModule,
    AngularDraggableModule,
    BrowserAnimationsModule,
    ScrollbarModule
  ],
  providers: [MapService, ApiService, GlobalDataService, PipelineService, UserService, RendererService],
  bootstrap: [AppComponent]
})
export class AppModule { }


