    loadEsri(): Promise<any> {
        // only load the ArcGIS API for JavaScript when this component is loaded
        const options = {
            url: 'https://js.arcgis.com/4.8/'
        };

        return loadModules([
            'esri/Map',
            'esri/views/MapView',
            'esri/widgets/Search',
            "esri/widgets/BasemapToggle",
            "esri/widgets/Zoom",
            "esri/layers/MapImageLayer",
            "esri/tasks/IdentifyTask",
            "esri/tasks/support/IdentifyParameters",
            "esri/Graphic",
            "esri/layers/GraphicsLayer",
            "esri/layers/FeatureLayer"
        ], options)
            .then(([EsriMap, EsriMapView, Search, BasemapToggle, Zoom, MapImageLayer, IdentifyTask, IdentifyParameters, Graphic, GraphicsLayer, FeatureLayer]) => {
                this.esriMap = EsriMap;
                this.esriMapView = EsriMapView;
                this.searchWidget = Search;
                this.basemapToggleWidget = BasemapToggle;
                this.zoomWidget = Zoom;
                this.mapImageLayer = MapImageLayer;
                this.identifyTask = IdentifyTask;
                this.identifyParameters = IdentifyParameters;
                this.graphic = Graphic;
                this.graphicsLayer = GraphicsLayer;
                this.featureLayer = FeatureLayer;
                this._maps.map1.map = this.esriMap({
                    basemap: 'streets'
                });

                this.highlightSelectedGraphicsLayer = new this.graphicsLayer();
                return Promise.resolve();
            })
            .catch(err => {
                console.error(err);
            });
    }

