import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import { Pipeline } from '../../app/models/pipeline.model';

import { ApiService } from './api.service';

@Injectable()
export class PipelineService {

  private serviceUrl = '';

  constructor(private apiService: ApiService, private http: HttpClient) 
  {

  }

  getPipelineData():Observable<Pipeline[]> {
    //return this.http.get<Pipeline[]>("http://localhost:8080/SpringRESTFulExample_19May2018/user/");
    return this.http.get<Pipeline[]>("http://localhost:8080/SpringRESTFulExample/pipelinedata/");
  }

  getPipelineDataOld():Observable<Pipeline[]>{
    //let dataOuput: any;
    return this.apiService.fetch('getLayerAttributeData', {}).map(resp => resp.json())
    .subscribe((data) => {
      return <Pipeline[]>data;
    });
    //return dataOuput;


    //http://localhost:8080/SpringRESTFulExample_19May2018/user/
  }


  getPipelineDataOld2(){
    //let dataOuput: any;
    return this.apiService.fetch('getLayerAttributeData', {}).map(resp => resp.json())
    .subscribe((data) => {
      return <Pipeline[]>data;
    });
  }

  getPipelineDataOld3():Observable<Pipeline[]> {
    //let dataOuput: any;
    let pipelineData : Pipeline[];

    this.apiService.fetch('getLayerAttributeData', {}).map(resp => resp.json())
    .subscribe((data) => {
      pipelineData = data;
    });


    return Observable.of(pipelineData).delay(500);
    //return dataOuput;


    //http://localhost:8080/SpringRESTFulExample_19May2018/user/
  }
}



